"use client";
import React, { useState } from "react";
import { Modal } from "@/components/ui/modal";
import PageBreadcrumb from "@/components/common/PageBreadCrumb";
import { FiUpload, FiDownload, FiEdit } from "react-icons/fi";

interface DocumentItem {
    id: string;
    name: string;
    category: string;
    uploadedOn: string;
}

const initialDocuments: DocumentItem[] = [
    { id: "1", name: "Employment Contract.pdf", category: "Employee", uploadedOn: "2025-01-15" },
    { id: "2", name: "PF Statement.pdf", category: "PF", uploadedOn: "2025-03-10" },
    { id: "3", name: "Resume.pdf", category: "Resume", uploadedOn: "2025-02-05" },
    { id: "4", name: "Degree Certificate.pdf", category: "Education", uploadedOn: "2024-12-20" },
    { id: "5", name: "Payslip Jan 2025.pdf", category: "Payslips", uploadedOn: "2025-01-31" },
];

const categories = ["Employee", "PF", "Education", "Payslips", "Resume"];

const EmployeeDocumentHub: React.FC = () => {
    const [documents, setDocuments] = useState<DocumentItem[]>(initialDocuments);
    const [selectedDocument, setSelectedDocument] = useState<DocumentItem | null>(null);
    const [isOpen, setIsOpen] = useState(false);
    const [fileName, setFileName] = useState("");
    const [fileCategory, setFileCategory] = useState("");

    const openModal = (doc?: DocumentItem) => {
        if (doc) {
            setSelectedDocument(doc);
            setFileName(doc.name);
            setFileCategory(doc.category);
        } else {
            setSelectedDocument(null);
            setFileName("");
            setFileCategory("");
        }
        setIsOpen(true);
    };

    const closeModal = () => {
        setIsOpen(false);
        setSelectedDocument(null);
        setFileName("");
        setFileCategory("");
    };

    const handleSave = () => {
        if (!fileName || !fileCategory) {
            alert("Please enter file name and select category");
            return;
        }

        if (selectedDocument) {
            // Update document
            setDocuments((prev) =>
                prev.map((doc) =>
                    doc.id === selectedDocument.id
                        ? { ...doc, name: fileName, category: fileCategory }
                        : doc
                )
            );
        } else {
            // Add new document
            setDocuments((prev) => [
                ...prev,
                { id: Date.now().toString(), name: fileName, category: fileCategory, uploadedOn: new Date().toISOString().split("T")[0] },
            ]);
        }

        closeModal();
    };

    return (
        <div className="px-4 lg:px-10 py-6">
            {/* Breadcrumb */}
            <PageBreadcrumb pageTitle="Employee Document Hub" />

            {/* Header */}
            <div className="flex justify-between items-center mb-6">
                <button
                    onClick={() => openModal()}
                    className="flex items-center gap-2 px-4 py-2 bg-brand-500 text-white rounded-lg hover:bg-brand-600"
                >
                    <FiUpload /> Upload Document
                </button>
            </div>

            {/* Document Sections */}
            {categories.map((cat) => {
                const docs = documents.filter((d) => d.category === cat);
                return (
                    <div key={cat} className="mb-8">
                        <h3 className="text-lg font-semibold text-gray-700 dark:text-white mb-4">{cat}</h3>
                        {docs.length > 0 ? (
                            <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
                                {docs.map((doc) => (
                                    <div key={doc.id} className="bg-white dark:bg-gray-900 border border-gray-200 dark:border-gray-700 rounded-lg p-4 shadow-sm flex flex-col justify-between">
                                        <div>
                                            <p className="font-medium text-gray-800 dark:text-white truncate">{doc.name}</p>
                                            <p className="text-xs text-gray-500 dark:text-gray-400 mt-1">Uploaded on: {doc.uploadedOn}</p>
                                        </div>
                                        <div className="flex gap-3 mt-4">
                                            <button className="text-green-500 hover:text-green-600">
                                                <FiDownload size={20} />
                                            </button>
                                            <button onClick={() => openModal(doc)} className="text-blue-500 hover:text-blue-600">
                                                <FiEdit size={20} />
                                            </button>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        ) : (
                            <p className="text-gray-500 dark:text-gray-400">No documents uploaded yet.</p>
                        )}
                    </div>
                );
            })}

            {/* Upload/Update Modal */}
            <Modal isOpen={isOpen} onClose={closeModal} className="max-w-md p-6 lg:p-10">
                <div className="flex flex-col gap-4">
                    <h2 className="text-xl font-semibold text-gray-800 dark:text-white">{selectedDocument ? "Update Document" : "Upload Document"}</h2>

                    <div>
                        <label className="block text-sm font-medium text-gray-700 dark:text-gray-400">File Name *</label>
                        <input
                            type="text"
                            value={fileName}
                            onChange={(e) => setFileName(e.target.value)}
                            className="w-full rounded-lg border border-gray-300 px-3 py-2 dark:border-gray-700 dark:bg-gray-900 dark:text-white"
                        />
                    </div>

                    <div>
                        <label className="block text-sm font-medium text-gray-700 dark:text-gray-400">Category *</label>
                        <select
                            value={fileCategory}
                            onChange={(e) => setFileCategory(e.target.value)}
                            className="w-full rounded-lg border border-gray-300 px-3 py-2 dark:border-gray-700 dark:bg-gray-900 dark:text-white"
                        >
                            <option value="">Select Category</option>
                            {categories.map((cat) => (
                                <option key={cat} value={cat}>{cat}</option>
                            ))}
                        </select>
                    </div>

                    <div className="flex justify-end gap-3 mt-4">
                        <button onClick={closeModal} className="px-4 py-2 border rounded-lg border-gray-300 dark:border-gray-700 dark:text-white">Cancel</button>
                        <button onClick={handleSave} className="px-4 py-2 bg-brand-500 text-white rounded-lg hover:bg-brand-600">{selectedDocument ? "Update" : "Upload"}</button>
                    </div>
                </div>
            </Modal>
        </div>
    );
};

export default EmployeeDocumentHub;
